export function autoFontSize () {
  let docEl = document.documentElement
  document.getElementsByTagName('html')[0].style.fontSize = (docEl.clientWidth / 375) * 20 + 'px'
  window.onresize = function () {
    document.getElementsByTagName('html')[0].style.fontSize = (docEl.clientWidth / 375) * 20 + 'px'
  }
}
